"use client"
import { CiSearch } from "react-icons/ci";
import { LuBell } from "react-icons/lu";
import Image from "next/image";
import { inter } from "@/app/layout";
import Link from "next/link";
import mainLogo from "@/public/assets/Logo/Logo-Small-Light.png";
const NavBar = () => {
  return (
    <div className="w-full bg-[var(--richblack-900)] h-[56px] border-b-[1px] border-[var(--richblack-700)]">
      <div className="navbar shadow-sm w-11/12 h-full mx-auto py-3 min-[810px]:px-[120px] flex items-center">

        <div className="navbar-start">
          <div className="dropdown">
            <div tabIndex={0} role="button" className="btn btn-ghost lg:hidden">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h8m-8 6h16" />
              </svg>
            </div>
            <ul tabIndex={0} className={`menu menu-sm dropdown-content bg-base-100 rounded-box z-1 mt-3 w-52 p-2 shadow ${inter.variable} font-sans font-normal text-base leading-6 text-[var(--richblack-25)]`}>
              <li><Link className="font-sans font-semibold text-base leading-6 text-[var(--yellow-50)]" href={"/"}>Home</Link></li>
              <li>
                <summary>Catalog</summary>
                <ul className="p-2">
                  <li><a>Submenu 1</a></li>
                  <li><a>Submenu 2</a></li>
                </ul>
              </li>
              <li><a>About Us</a></li>
              <li><a>Contact Us</a></li>
              <li className="flex items-center flex-row-reverse w-full justify-between">
                <button className="btn btn-ghost btn-circle">
                  <CiSearch className="w-5 h-5 fill-[var(--richblack-200)]"/>
                </button>
                <input
                type="text"
                placeholder="Search"
                className="input input-bordered w-[150px] h-6"
                />
              </li>
            </ul>
          </div>
          <Link
            className="hover:cursor-pointer hover:bg-transparent hover:border-none text-xl
            w-[160px] h-[32px] text-[var(--pure-greys-5)] font-semibold flex gap-2"
            href="/"
          >
            <Image src={mainLogo} alt="StudyLiv Logo" width={32} height={32}/>
            <p className="block max-[820px]:hidden">StudyLiv</p>
          </Link>
        </div>
        
        <div className="navbar-center hidden lg:flex">
          <ul className={`menu menu-horizontal px-1 ${inter.variable} font-sans font-normal text-base leading-6 text-[var(--richblack-25)]`}>
            <li><Link className="font-sans font-semibold text-base leading-6 text-[var(--yellow-50)] hover:bg-transparent" href={"/"}>Home</Link></li>
            <li>
              <details className="hover:bg-transparent">
                <summary  className="hover:bg-transparent">Catalog</summary>
                <ul className="p-2 w-[200px]">
                  <li><a>Submenu 1</a></li>
                  <li><a>Submenu 2</a></li>
                </ul>
              </details>
            </li>
            <li><a  className="hover:bg-transparent">About Us</a></li>
            <li><a  className="hover:bg-transparent">Contact Us</a></li>
          </ul>
        </div>

        <div className="navbar-end">

          <div className="flex gap-2">
            <div className="flex items-center max-[600px]:hidden">
              <button className="btn btn-ghost btn-circle">
                <CiSearch className="w-5 h-5 fill-[var(--richblack-200)]"/>
              </button>
              <input
                type="text"
                placeholder="Search"
                className="input input-bordered w-16 h-6 md:w-auto"
              />
            </div>
            
            <div className="dropdown dropdown-end">
              <div tabIndex={0} role="button" className="btn btn-ghost btn-circle avatar">
                <div className="w-10 rounded-full">
                  <Image
                    alt="User Avatar"
                    src={"https://img.daisyui.com/images/stock/photo-1534528741775-53994a69daeb.webp"}
                    width={20}
                    height={20}
                  />
                </div>
              </div>
              <ul tabIndex={0} className="menu menu-sm dropdown-content bg-base-100 rounded-box z-1 mt-3 w-52 p-2 shadow">
                <li>
                  <a className="justify-between">
                    Profile
                    <span className="badge">New</span>
                  </a>
                </li>
                <li><a>Settings</a></li>
                <li><a>Logout</a></li>
              </ul>
            </div>
            
            <button className="btn btn-ghost btn-circle">
                <div className="indicator">
                    <LuBell className="w-5 h-5 font-extrabold fill-white"/>
                </div>
            </button>

          </div>

        </div>

      </div>
    </div>
  );
};

export default NavBar;

